'use client'
import * as d3 from "d3"
import cloud from "d3-cloud"
import { useEffect, useRef, useState } from "react"

export default function WordCloud() {
    const containerRef = useRef(null)
    const svgRef = useRef(null)
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(null)
    const [dimensions, setDimensions] = useState({ width: 800, height: 600 })
    const wordDataRef = useRef(null)

    // Handle resize
    useEffect(() => {
        function updateDimensions() {
            if (containerRef.current) {
                const { width, height } = containerRef.current.getBoundingClientRect()
                setDimensions({ 
                    width: width || 800, 
                    height: height || 600 
                })
            }
        }

        updateDimensions()
        const resizeObserver = new ResizeObserver(updateDimensions)
        
        if (containerRef.current) {
            resizeObserver.observe(containerRef.current)
        }

        return () => {
            resizeObserver.disconnect()
        }
    }, [])

    // Load transcript data
    useEffect(() => {
        async function loadData() {
            try {
                const response = await fetch('/api/transcripts')
                if (!response.ok) {
                    throw new Error('Failed to fetch transcripts')
                }
                const transcripts = await response.json()

                // Extract all words from transcripts
                const allWords = []
                transcripts.forEach(transcript => {
                    if (Array.isArray(transcript)) {
                        transcript.forEach(wordObj => {
                            if (wordObj.w) {
                                allWords.push(wordObj.w)
                            }
                        })
                    } else if (transcript.transcript) {
                        allWords.push(transcript.transcript)
                    }
                })

                // Process words: remove punctuation, convert to lowercase, filter
                const words = allWords
                    .map(word => word.toLowerCase().replace(/[^\w]/g, ''))
                    .filter(word => word.length > 3) // Filter out short words and empty strings

                // Count word frequencies
                const wordCounts = {}
                words.forEach(word => {
                    wordCounts[word] = (wordCounts[word] || 0) + 1
                })

                // Convert to array and sort by frequency
                const data = Object.entries(wordCounts)
                    .map(([text, value]) => ({ text, value }))
                    .sort((a, b) => b.value - a.value)
                    .slice(0, 100)

                wordDataRef.current = data
                setLoading(false)
            } catch (err) {
                console.error('Error loading transcripts:', err)
                setError(err.message)
                setLoading(false)
            }
        }

        loadData()
    }, [])

    // Render word cloud when dimensions or data changes
    useEffect(() => {
        if (loading || error || !wordDataRef.current) return

        const { width, height } = dimensions

        // Create word cloud layout
        const layout = cloud()
            .size([width, height])
            .words(wordDataRef.current.map(d => ({
                text: d.text,
                size: 10 + (d.value * 5) // Scale font size based on frequency
            })))
            .padding(5)
            .rotate(() => 0)
            .font("Arial")
            .fontSize(d => d.size)
            .on("end", draw)

        layout.start()

        function draw(words) {
            const svg = d3.select(svgRef.current)
            svg.selectAll("*").remove()

            svg.attr("width", width)
                .attr("height", height)
                .attr("viewBox", `0 0 ${width} ${height}`)
                .style("max-width", "100%")
                .style("height", "auto")

            const g = svg.append("g")
                .attr("transform", `translate(${width / 2},${height / 2})`)

            g.selectAll("text")
                .data(words)
                .enter()
                .append("text")
                .style("font-size", d => `${d.size}px`)
                .style("font-family", "Arial")
                .style("fill", (d, i) => d3.schemeCategory10[i % 10])
                .attr("text-anchor", "middle")
                .attr("transform", d => `translate(${d.x},${d.y})rotate(${d.rotate})`)
                .text(d => d.text)
        }
    }, [dimensions, loading, error])

    if (loading) {
        return <div>Loading word cloud...</div>
    }

    if (error) {
        return <div>Error: {error}</div>
    }

    return (
        <div ref={containerRef} style={{ width: '100%', height: '100%', minHeight: '400px' }}>
            <svg ref={svgRef} style={{ width: '100%', height: '100%' }}></svg>
        </div>
    )
}
